ute;\n    top: 0;\n    width: 10px;\n    height: 10px;\n    border-radius: 5px;\n    background-color: #9880ff;\n    color: #9880ff;\n}\n\n.dot-pulse::before {\n    box-shadow: 9984px 0 0 -5px #9880ff;\n    animation: dot-pulse-before 1.5s infinite linear;\n    animation-delay: 0s;\n}\n\n.dot-pulse::after {\n    box-shadow: 10014px 0 0 -5px #9880ff;\n    animation: dot-pulse-after 1.5s infinite linear;\n    animation-delay: .5s;\n}\n\n@keyframes dot-pulse-before {\n    0% {\n        box-shadow: 9984px 0 0 -5px #9880ff;\n    }\n    30% {\n        box-shadow: 9984px 0 0 2px #9880ff;\n    }\n    60%,\n    100% {\n        box-shadow: 9984px 0 0 -5px #9880ff;\n    }\n}\n\n@keyframes dot-pulse {\n    0% {\n        box-shadow: 9999px 0 0 -5px #9880ff;\n    }\n    30% {\n        box-shadow: 9999px 0 0 2px #9880ff;\n    }\n    60%,\n    100% {\n        box-shadow: 9999px 0 0 -5px #9880ff;\n    }\n}\n\n@keyframes dot-pulse-after {\n    0% {\n        box-shadow: 10014px 0 0 -5px #9880ff;\n    }\n    30% {\n        box-shadow: 10014px 0 0 2px #9880ff;\n    }\n    60%,\n    100% {\n        box-shadow: 10014px 0 0 -5px #9880ff;\n    }\n}\n\n.pagination li {\n    cursor: pointer;\n    border: 1px solid;\n    margin: 1em;\n    padding: 0.25em;\n}\n\n.disabled {\n    pointer-events: none;\n    color: #999;\n}\n\ntfoot.tbl_pagination tr td {\n    text-align: center;\n    font-weight: bold;\n}\n\ntfoot.tbl_pagination tr td .fa {\n    background: #999;\n    color: #fff;\n    width: 25px;\n    height: 25px;\n    font-style: initial;\n    font-size: 1.5em;\n    font-weight: bold;\n    text-align: center;\n    line-height: 25px;\n    border-radius: 0.3em;\n}\n\ntfoot.tbl_pagination tr td select {\n    color: #2a2b41;\n}\n\ntfoot.tbl_pagination tr td .fa:hover {\n    background: #2a2b41;\n    cursor: pointer;\n}\n\ntfoot.tbl_pagination tr td ul {\n    display: flex;\n    flex-direction: row;\n    width: 100%;\n    margin: 0px;\n    padding: 0px;\n    justify-content: space-around;\n}\n\ntf